import { WebGPUShaderCompileCode, enumDescribe, enumInOut, enumOperator } from "./WebGPUShaderCompileCode";
import { WebGPUShaderCompileUtil } from "./WebGPUShaderCompileUtil";
export class WebGPUShaderToken {
    constructor(includefiles) {
        this.z = 0;
        if (includefiles) {
            this.includefiles = includefiles;
        }
        else {
            this.includefiles = [];
        }
    }
    condition(def) {
        if ('#else' == this.name)
            return true;
        if (null != def) {
            if ("#ifdef" == this.name || "#ifndef" == this.name) {
                try {
                    let check = this.defParam[0];
                    return ("#ifdef" == this.name) == !!def[check];
                }
                catch (err) { }
            }
            else {
                if (!(this.defParam instanceof WebGPUShaderToken)) {
                    let defParm = this.defParam.join(" ");
                    let parmRoot = WebGPUShaderCompileCode.compile(defParm);
                    this.defParam = parmRoot;
                }
                return WebGPUShaderCompileUtil.checkCondition(this.defParam, def);
            }
        }
        else if ("#ifndef" == this.name) {
            return true;
        }
        return false;
    }
    addParameterArr(param, parent) {
        if (null == this.parameterArr) {
            this.parameterArr = [];
        }
        this.parameterArr.push(param);
        param.owner = this;
        if (parent) {
            param.parent = parent;
        }
    }
    setParameter(param, parent) {
        this.parameter = param;
        param.owner = this;
        if (parent)
            param.parent = parent;
    }
    addBody(body) {
        body.setParent(this);
    }
    setParent(parent) {
        if (null == parent.childs) {
            parent.childs = [];
        }
        parent.childs.push(this);
        this.z = parent.z + 1;
        this.parent = parent;
    }
    _parseShaderNode(sn) {
        let ret = '';
        let operator = null;
        if (null != sn.operator) {
            operator = enumOperator[sn.operator];
            if (!sn.operatorRight) {
                ret += operator;
                operator = null;
            }
        }
        if (sn.type) {
            if (null != sn.describe) {
                ret += enumDescribe[sn.describe] + " ";
            }
            if (null != sn.inOrOut) {
                ret += enumInOut[sn.inOrOut] + " ";
            }
            if (null != sn.type) {
                ret += sn.type + " ";
            }
            if (null != sn.name) {
                ret += sn.name;
            }
            if (null != sn.parameter) {
                let childs = sn.parameter.childs;
                if (childs) {
                    ret += '(';
                    for (let i = 0, len = childs.length; i < len; i++) {
                        ret += this._parseShaderNode(childs[i]);
                    }
                    ret += ')';
                }
            }
        }
        else if (sn.parameter) {
            if (null != sn.name) {
                ret += sn.name;
            }
            if (null != operator) {
                ret += operator;
                operator = null;
            }
            ret += '(' + this._getParameter(sn.parameter) + ")";
        }
        else if (sn.parameterArr) {
            ret += this._getParameterArr(sn, '');
        }
        else if (sn.name) {
            ret += sn.name;
        }
        if (null != operator) {
            ret += operator;
        }
        if (null != sn.childs) {
            for (let i = 0, len = sn.childs.length; i < len; i++) {
                ret += this._parseShaderNode(sn.childs[i]);
            }
        }
        return ret;
    }
    _getParameter(param, isFor = false) {
        let ret = '';
        if (null == param) {
            param = this.parameter;
        }
        if (param) {
            if (param.childs) {
                let arr = param.childs;
                for (let i = 0, len = arr.length; i < len; i++) {
                    let sn = arr[i];
                    ret += this._parseShaderNode(sn);
                    if (isFor && i < len - 1) {
                        ret += ';';
                    }
                }
            }
            else {
                ret += this._parseShaderNode(param);
            }
        }
        return ret;
    }
    _getParameterArr(st, end = ';') {
        let outStr = '';
        if (null != st.type) {
            outStr += st.type + " ";
        }
        let operator;
        if (null != st.operator && '' != end) {
            operator = enumOperator[st.operator];
            if (!st.operatorRight) {
                outStr += operator;
                operator = null;
            }
        }
        let arr = st.parameterArr;
        if (null != st.name) {
            outStr += st.name;
        }
        for (let i = 0, len = arr.length; i < len; i++) {
            outStr += "[" + st._getParameter(arr[i]) + "]";
        }
        if (null != operator) {
            outStr += operator;
        }
        if (st.assign) {
            if (null != st.assignLeft) {
                outStr += enumOperator[st.assignLeft];
            }
            outStr += "=";
        }
        if (st.childs) {
            outStr += st._getParameter(st);
        }
        return outStr + end;
    }
    toscript(def, out) {
        if (null == out) {
            out = [];
        }
        if (this.type) {
            if ("return" == this.type) {
                let outStr = this.type + " ";
                if (this.name) {
                    outStr += this.name;
                }
                if (this.parameter) {
                    outStr += "(" + this._getParameter() + ")";
                }
                if (null != this.childs) {
                    outStr += this._getParameter(this);
                }
                outStr += ";";
                out.push(outStr);
            }
            else if (this.parameter) {
                let outStr = this.type + " " + this.name + "(" + this._getParameter() + "){";
                if (null != this.describe) {
                    outStr = enumDescribe[this.describe] + " " + outStr;
                }
                out.push(outStr);
                if (this.childs) {
                    for (let i = 0, len = this.childs.length; i < len; i++) {
                        this.childs[i].toscript(def, out);
                    }
                }
                out.push('}');
            }
            else if (this.parameterArr) {
                out.push(this._getParameterArr(this));
            }
            else if (this.assign) {
                let outStr = this.type + " " + this.name;
                if (null != this.assignLeft) {
                    outStr += enumOperator[this.assignLeft];
                }
                outStr += '=';
                if (this.childs) {
                    outStr += this._getParameter(this);
                    outStr += ';';
                    out.push(outStr);
                }
                else {
                    console.log("");
                }
            }
            else if (null != this.name) {
                let outStr = '';
                if (null != this.describe) {
                    outStr += enumDescribe[this.describe] + " ";
                }
                outStr += this.type + " " + this.name;
                if (null != this.operator) {
                    outStr += enumOperator[this.operator];
                }
                if ('struct' == this.type) {
                    outStr += '{';
                    out.push(outStr);
                    outStr = '';
                    if (null != this.childs) {
                        let arr = this.childs;
                        for (let i = 0, len = arr.length; i < len; i++) {
                            let sn = arr[i];
                            let outStr = this._parseShaderNode(sn);
                            if ('' != outStr) {
                                out.push(outStr + ";");
                            }
                        }
                    }
                    outStr += '}';
                    if (this.varNames) {
                        outStr += " " + this.varNames.join(",");
                    }
                }
                else {
                    if (null != this.childs)
                        outStr += this._getParameter(this);
                }
                outStr += ';';
                out.push(outStr);
            }
            else {
                out.push(this.type + ";");
            }
        }
        else if (this.parameterArr) {
            out.push(this._getParameterArr(this));
        }
        else if (this.parameter) {
            if (null != this.name) {
                let outStr = '';
                if (null != this.operator) {
                    outStr += enumOperator[this.operator];
                }
                outStr += this.name + "(" + this._getParameter(null, 'for' == this.name) + ")";
                if ("layout" == this.name) {
                    if (this.childs) {
                        for (let i = 0, len = this.childs.length; i < len; i++) {
                            let sn = this.childs[i];
                            if (sn.describe == enumDescribe.uniform) {
                                outStr += ' uniform';
                            }
                            else {
                                console.log("TODO:", sn);
                            }
                        }
                    }
                    outStr += ';';
                    out.push(outStr);
                }
                else {
                    out.push(outStr);
                    if (this.childs) {
                        out.push("{");
                        for (let i = 0, len = this.childs.length; i < len; i++) {
                            let sn = this.childs[i];
                            sn.toscript(def, out);
                        }
                        out.push("}");
                    }
                }
            }
        }
        else if (this.name) {
            if (this.describe == enumDescribe.uniform) {
                out.push("uniform " + this.name + "{");
                for (let i = 0, len = this.childs.length; i < len; i++) {
                    let sn = this.childs[i];
                    sn.toscript(def, out);
                }
                let outstr = '}';
                if (this.varNames) {
                    outstr += " " + this.varNames.join(",");
                }
                outstr += ';';
                out.push(outstr);
            }
            else if (this.assign) {
                let outStr = this.name;
                if (null != this.assignLeft) {
                    outStr += enumOperator[this.assignLeft];
                }
                outStr += '=';
                if (this.childs) {
                    outStr += this._getParameter(this);
                    outStr += ';';
                    out.push(outStr);
                }
                else {
                    console.log("");
                }
            }
            else {
                if (this.childs) {
                    out.push(this.name + "{");
                    for (let i = 0, len = this.childs.length; i < len; i++) {
                        let sn = this.childs[i];
                        sn.toscript(def, out);
                    }
                    out.push("}");
                }
                else {
                    if (0 > this.name.indexOf("#")) {
                        out.push(this.name + ";");
                    }
                    else {
                        out.push(this.name);
                    }
                }
            }
        }
        else if (null != this.operator) {
        }
        else if (this.childs) {
            if (null != this.parent) {
                out.push("{");
                for (let i = 0, len = this.childs.length; i < len; i++) {
                    let sn = this.childs[i];
                    sn.toscript(def, out);
                }
                out.push("}");
            }
            else {
                for (let i = 0, len = this.childs.length; i < len; i++) {
                    let sn = this.childs[i];
                    sn.toscript(def, out);
                }
            }
        }
        else {
        }
        return out;
    }
}
