import { RenderCMDType, SetShaderDefineCMD } from "../../DriverDesign/RenderDevice/IRenderCMD";
export class WebGPUSetShaderDefine extends SetShaderDefineCMD {
    get define() {
        return this._define;
    }
    set define(value) {
        this._define = value;
    }
    get dest() {
        return this._dest;
    }
    set dest(value) {
        this._dest = value;
    }
    get add() {
        return this._add;
    }
    set add(value) {
        this._add = value;
    }
    constructor() {
        super();
        this.type = RenderCMDType.ChangeShaderDefine;
    }
    apply(context) {
        if (this.add)
            this._dest.addDefine(this.define);
        else
            this._dest.removeDefine(this.define);
    }
}
