export class WebGPUShaderDefine {
    static findNumberDefine(code, map) {
        const pattern = /^\s*#define\s+(\w+)\s+([1-9]\d*)(?=\s*($|\/\/))/gm;
        if (!map)
            map = new Map();
        let match;
        while ((match = pattern.exec(code)) !== null) {
            map.set(match[1], match[2]);
        }
        return map;
    }
}
