import { _clearCR } from "./WebGPUShaderCompileCode";
import { WebGPUShaderCompileUtil } from "./WebGPUShaderCompileUtil";
import { WebGPUShaderToken } from "./WebGPUShaderToken";
export class WebGPUShaderCompileDef {
    static compile(code, defs) {
        code = code.replace(_clearCR, "");
        this._defs = defs;
        let st = new WebGPUShaderToken();
        this._compileToTree(st, code);
        this._parentNode = null;
        this._defs = null;
        this._currNode = null;
        return st;
    }
    static isEmptyNode(node) {
        for (let name in node) {
            if ('includefiles' == name || 'owner' == name || 'z' == name) {
                continue;
            }
            return false;
        }
        return true;
    }
    static nextCurrNode(isForceCreate = false) {
        if (isForceCreate) {
            this._currNode = null;
            return this.currNode;
        }
        else {
            if (null != this._currNode) {
                if (this.isEmptyNode(this._currNode)) {
                    return null;
                }
                this._currNode = null;
            }
            return null;
        }
    }
    static get currNode() {
        if (null == this._currNode) {
            this._currNode = new WebGPUShaderToken(this._parentNode.includefiles);
            this._parentNode.addBody(this._currNode);
        }
        return this._currNode;
    }
    static _compileToTree(parent, code) {
        this._parentNode = parent;
        let lines = code.split("\n");
        for (let i = 0, len = lines.length; i < len; i++) {
            let text = lines[i];
            if (text.length < 1)
                continue;
            let ofs = text.indexOf("//");
            if (0 < ofs) {
                this._parseNode(text.substring(0, ofs));
                this._parseNode(text.substring(ofs));
            }
            else {
                this._parseNode(text);
            }
        }
    }
    static _parseNode(text) {
        text = text.split("\t").join(" ").trim();
        if (text.indexOf("#") != 0) {
            if (null == this.currNode.code) {
                this.currNode.code = text;
            }
            else {
                this.currNode.code += '\n' + text;
            }
        }
        else {
            let arr = text.split(" ");
            let name = arr.shift();
            if ("#endif" == name) {
                this._parentNode = this._parentNode.parent;
                this._currNode = null;
                return;
            }
            let node;
            switch (name) {
                case '#ifdef':
                case "#ifndef":
                case "#if":
                    node = this.nextCurrNode(true);
                    node.code = text;
                    node.name = name;
                    node.defParam = arr;
                    this._parentNode = node;
                    this._currNode = null;
                    WebGPUShaderCompileUtil.checkDef(node, this._defs);
                    break;
                case "#elif":
                case "#else":
                    this._parentNode = this._parentNode.parent;
                    node = this.nextCurrNode(true);
                    node.code = text;
                    node.name = name;
                    node.defParam = arr;
                    this._parentNode = node;
                    this._currNode = null;
                    if ('#elif' == name)
                        WebGPUShaderCompileUtil.checkDef(node, this._defs);
                    break;
                case "#include":
                    break;
                default:
                    node = this.nextCurrNode(true);
                    node.code = text;
                    node.name = name;
                    node.defParam = arr;
                    this._currNode = null;
                    break;
            }
        }
    }
}
